.footer {
  padding: 50px 0px 50px;
  background-color: $mainColor;
  color: $bodyColor;

  // .footer__container

  &__container {}

  // .footer__grid

  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  }

  // .footer__col

  &__col {
    display: flex;
    flex-direction: column;

    &:nth-child(2) {
      justify-self: center;

      @media screen and (max-width: 610px) {
        justify-self: flex-start;
      }
    }

    &:nth-child(3) {
      justify-self: end;
      text-align: right;

      .footer__nav {
        a {
          justify-content: flex-end;
        }
      }

      @media screen and (max-width: 900px) {
        justify-self: start;
        text-align: left;

        .footer__nav {
          a {
            justify-content: flex-start;
          }
        }
      }
    }
  }

  // .footer__title

  &__title {
    font-size: 25px;
    font-weight: 900;
    margin-bottom: 40px;
  }

  // .footer__item

  &__item {

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    span {
      display: flex;
      font-size: 16px;
      color: rgba($color: $bodyColor, $alpha: 0.8);

      &:first-child {
        color: rgba($color: $bodyColor, $alpha: 1);
        margin-bottom: 10px;
        font-weight: 400;
      }
    }
  }

  // .footer__social

  &__social {
    display: flex;
    align-items: center;

    a {
      width: 40px;
      height: 40px;
      display: flex;

      &:nth-child(2) {
        height: 35px;
      }

      &:not(:last-child) {
        margin-right: 10px;
      }

      img,
      svg,
      i {
        max-width: 100%;
      }
    }
  }

  // .footer__nav

  &__nav {
    display: flex;
    flex-direction: column;

    a {
      display: flex;

      font-size: 16px;
      padding: 10px 0px;
      transition: $animation_speed;

      &:hover {
        color: $accent;
      }
    }
  }

  // .footer__logo

  &__logo {
    margin-bottom: 40px;

    img {
      width: 100px;
    }

    @media screen and (max-width: 900px) {
      display: none;
    }
  }


}


.author {
  padding: 30px 0px;
  background-color: $mainColor;
  border-top: 1px solid rgba($color: $white, $alpha: .2);

  // .author__container

  &__container {}

  // .author__inner
  &__inner {
    text-align: center;
    color: $white;
    font-size: 17px;
    transition: $animation_speed;


    &:hover {
      opacity: .8;
    }
  }
}